.saldo-wrap{
	width:100%;
	height:1.5rem;
	color:#fff;
	font-size:.5rem;
	position: relative;
	overflow: hidden;
}
.saldo-wrap .img-box{
	width:100%;
	height:100%;
	
}
.saldo-wrap .saldo-box{
	position: absolute;
	width:100%;
	height:2rem;
	padding:.1rem .3rem;
	top:0;
	left:0;
}
.saldo-wrap .saldo-box .btn{
	width:1.4rem;
	height:.8rem;
	line-height: 0.5rem;
	color:#BE06CF;
	box-shadow:0px 0px 2px 2px #fff;
	margin-top:.2rem;
}
.saldo-wrap .saldo-box img{
	width:.5rem;
	height:.5rem;
}
.saldo-wrap .saldo-box .left{
	margin-top:.2rem;
}
.saldo-wrap .saldo-box span{
	padding:0 .3rem;
	font-size:.3rem;
}

/*领红包  sss*/
.red-bag-wrap{
	padding:.3rem;
}
.red-bag-wrap .left,.red-bag-end-wrap .left{
	width:1rem;
	height:1rem;
}
.red-bag-wrap .right{
	width:80%;
	height:2rem;
	position:relative;
}
.red-bag-wrap .right .r-top{
	position:absolute;
	width:100%;
	top:.24rem;
	left:.6rem;
	font-size:.38rem;
	color:#fff;
}
.red-bag-wrap .right .r-top img{
	width:.64rem;
	margin-right:.3rem;
}
.red-bag-wrap .right .r-bottom{
	position: absolute;
	width:100%;
	left:.6rem;
	bottom:.2rem;
	font-size:.3rem;
	color:#999999;
}
.room-wrap-box .label-default{
	background-color: #364066;
    font-size: .35rem;
    color: #A3AAC0;
    width: 72%;
    display: block;
	padding:.2rem 0;
    text-align: center;
    margin:.3rem 0 .3rem 24%;
}
.red-bag-end-wrap .right{
	width: 80%;
    /* height: 2rem; */
    color: #fff;
    font-size: .35rem;
    background: #364066;
    position: relative;
    padding:.2rem .4rem;
}
.red-bag-end-wrap .right:after{
	content: '';
	display: block;
	position:absolute;
	left:-.1rem;
	top:.2rem;
	background: #364066;
	width:.4rem;
	height:.4rem;
	transform:rotate(45deg);
-ms-transform:rotate(45deg); 	/* IE 9 */
-moz-transform:rotate(45deg); 	/* Firefox */
-webkit-transform:rotate(45deg); /* Safari 和 Chrome */

}
.red-bag-end-wrap{
	padding:.3rem;
}
/*幸运红包sssss*/

.boa-red-bag{
	    position: fixed;
    bottom: .3rem;
    right: .3rem;
    text-align: center;
	
}
.boa-red-bag .img-box{
	width:2.4rem;
}
.boa-red-bag .img-box span{
	    position: absolute;
    right: .82rem;
    top: 1.3rem;
    font-size: 12px;
    color: #AE5C12;
    transform: rotate(17deg);
    -ms-transform: rotate(17deg);
    -moz-transform: rotate(17deg);
    -webkit-transform: rotate(17deg);
}
.boa-red-bag .boa-rule{
	font-size:.3rem;
	color:#A3AAC0;
	text-decoration: underline;
}
