.alert-wrap{
	position:fixed;
	width: 100%;

	margin:0 auto;
	min-height: 100%;
	background: raga(0,0,0,.7);
	top:0;
	left:0;
}
.alert-wrap .alert-box{
	/*position:absolute;*/
	margin:25% auto;
	left:10%;
	top:1rem;
	width:80%;
	max-width: 800px;
	padding:.3rem;
	font-size:.5rem;
	color:#666;
	background: #fff;
	border-radius: .3rem;
}
.alert-wrap .alert-title{
	font-size:.7rem;
	color:#7A27C1;
	text-align: center;
	position: relative;
}
.alert-wrap .alert-title:after{
	content: '';
	position: absolute;
	left:55%;
	top:-.34rem;
	width:1rem;
	height:1rem;
	border-radius: 50%;
	background:linear-gradient(114deg,rgba(255,122,122,0.41),rgba(254,234,234,0.41));
}
.alert-wrap .alert-title:before{
	content: '';
	position: absolute;
	left:16%;
	top:-.5rem;
	width:1.2rem;
	height:1.2rem;
	border-radius: 50%;
	background:linear-gradient(114deg,rgba(243,208,251,0.99),rgba(251,234,254,0.99));
	opacity:0.7;
}
.alert-wrap span{
	color:#000;
}
.alert-cancel{
	text-align: right;
}
