.open-wrap{
	position:fixed;
	width:100%;
	margin: 0 auto;
	min-height: 100%;
	background: rgba(0,0,0,.7);
	top:0;
	left:0;
}
.open-wrap .open-box{
	    /* position: absolute; */
    width: 80%;
    /* top: 1rem; */
    max-width: 8rem;
    margin: 1rem auto;
    /* left: 10%; */
    text-align: center;
    font-size: .3rem;
    position: relative;
	
}
.open-wrap .open-box .open-intro{
	position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
.p1{
	margin: 0.3rem auto .5rem;
    font-weight: 600;
}
.boa-p1{
	color:#A36028;
}
.open-wrap .open-box .cancel-box{
	text-align: right;
	padding:0.3rem .3rem 0;
}
.open-wrap .open-box .cancel-box span{
	    color: #fff;
    font-size: .5rem;
}
.open-wrap .open-box .cancel-box .boa-cancel{
	color:#000;
}
.open-wrap .open-box .bag-icon{
	width: 1.5rem;
    display: block;
    margin: .8rem auto 0;
    border: .1rem solid #fff;
    border-radius: 50%;
}
@media (max-width:750px ) {
	.open-wrap .open-box .bag-icon{
		margin-top:1%;
	}
	/*.open-wrap .open-box .boa-pen-icon{
		margin-top:25%;
	}*/
}

.open-wrap .open-box .boa-icon{
	border:0;
}
.open-wrap .open-box .all-eos{
	color:#FF5050;
	font-size:.37rem;
}
.get-bag-list-wrap{
	padding:.3rem;
	color:#999999;
	font-size:.4rem;
	background: #fff;
}
.get-bag-list-wrap:first{
	border-bottom: .02rem dashed #E1E1E1;
	
}
